<template>
  <div class="Forget">
    <HomePageTop :TopTitle="TopTotle"></HomePageTop>
    <div class="pl30 pr30 mt80">
      <!-- 注册部分 -->
      <div>
        <!-- 第一行 -->
        <div class="both filling-in  border-bD8D8D8">
          <span class="fillingLeft">手机号</span>
          <div class="fl pt30 pb30">
            <input type="text" placeholder="请输入手机号" maxlength="11" v-model="AccountModel" name="PhoneNumber"
              autocomplete="off" oninput="value=value.replace(/[^\d]/g,'')" @blur="judge(0)">
          </div>
          <span class="reminder" v-if="PhoneTrue">{{numberText}}</span>
        </div>
        <div class="both filling-in border-bD8D8D8">
          <span class="fl fillingLeft">验证码</span>
          <div class="fl pt30 pb30">
            <input type="text" placeholder="请输入验证码" maxlength="4" v-model="verifyModel" name="VerifyNumber"
              autocomplete="off" class="w325" @blur="judge(1)">
          </div>
          <p class="icon-verification">
            <span @click="countDown" class="AccessEvents">{{count}}</span>
          </p>
          <span class="reminder" v-if="VerifyTrue">请输入验证码</span>
        </div>

        <div class="both filling-in border-bD8D8D8">
          <span class="fl fillingLeft">创建密码</span>
          <div class="fl pt30 pb30">
            <input :type="SetPassword==true?'password':'text'" v-model="PasswordSet" placeholder="请输入密码" name="PasswordNumber"
              autocomplete="off" @blur="judge(2)">
          </div>
          <p class="icon_paaword establish" @click="PasswordShow(1)">
            <img :src="SetPassword==true?passwordNo:passwordYes" alt="">
          </p>

        </div>
        <div class="both filling-in border-bD8D8D8">
          <span class="fl fillingLeft">确认密码</span>
          <div class="fl pt30 pb30">
            <input :type="PasswordAffirm==true?'password':'text'" v-model="AffirmPassword" placeholder="请输入密码" name="NewPasswordNumber"
              autocomplete="off" @blur="judge(3)">
          </div>
          <p class="icon_paaword affirm" @click="PasswordShow(2)">
            <img :src="PasswordAffirm==true?passwordNo:passwordYes" alt="">
          </p>
          <span class="reminder" v-if="PasswordTrue">{{passwordNumber}}</span>
        </div>
        <!-- 点击登录按钮 -->
        <div class="public-btn mt60 mb20" @click="submit">确认</div>

        <div class="both f28 cl_333">
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import Vue from "vue"
  import HomePageTop from "../common/HomePageTop";
  import $ from "jquery"
  export default {
    name: "Forget",
    components: {
      HomePageTop
    },
    data() {
      return {
        //像子组件传值
        TopTotle: {
          name: "忘记密码",
          left: true
        },
        textType: "text",
        //创建密码切换
        SetPassword: true,
        //确认密码切换
        PasswordAffirm: true,
        //  不可见图片
        passwordNo: require("../../assets/img/Register/passwordNo.png"),
        passwordYes: require("../../assets/img/Register/passwordYes.png"),
        //  账号
        AccountModel: "",
        //  验证码
        verifyModel: "",
        //创建密码
        PasswordSet: "",
        //确认密码
        AffirmPassword: "",
        //  手机号的错误弹窗
        PhoneTrue: false,
        //密码错误弹窗
        PasswordTrue: false,
        //验证码错误弹窗
        VerifyTrue: false,
        numberText: "手机号不能为空",
        passwordNumber: "密码不能为空",
        //  倒计时的时间数值
        count: "获取验证码",
      }
    },
    methods: {
      //获取验证码的倒计时
      countDown() {
        if (this.count == "获取验证码" || this.count == "验证码发完了") {
          let lastTime = 59;
          let timer = setInterval(() => {
            if (lastTime > 0) {
              this.count = "倒计时" + lastTime + "s";
              lastTime--;
            } else {
              window.clearInterval(timer);
              this.count = "验证码发完了"
            }
          }, 1000)

        } else {
          console.log("youshijianazainongba1");
          return;
        }
      },
      //点击确认
      submit() {
        if (this.AccountModel != "" && this.verifyModel != "" && this.PasswordSet != "" && this.AffirmPassword != "") {
          //首先校验密码是否一致
          if (this.AffirmPassword == this.PasswordSet) {
            //如果密码一致的话就说明成功了  然后再校验错误提示是否还在
            if (this.PhoneTrue == false && this.PasswordTrue == false && this.VerifyTrue == false) {
              alert("成功")
            }
          }
        }
      },
      //失去焦点
      judge(i) {
        console.log("111");
        let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (i == 0) {
          if (!myreg.test(this.AccountModel)) {
            if (this.AccountModel == "") {
              this.numberText = "手机号不能为空"
            } else {
              this.numberText = "请输入正确的格式"
            }
            this.PhoneTrue = true;
          } else {
            this.PhoneTrue = false
          }
        } else if (i == 1) {
          if (this.verifyModel == "" || this.verifyModel.length < 4) {
            this.VerifyTrue = true
          } else {
            this.VerifyTrue = false
          }
          //  确认密码
        } else if (i == 2) {
          if (this.PasswordSet == "") {
            this.passwordNumber = "密码不能为空";
            this.PasswordTrue = true
          } else {
            this.PasswordTrue = false
          }
        } else if (i == 3) {
          if (this.PasswordSet == "" || this.AffirmPassword == "") {
            this.PasswordTrue = true;
            this.passwordNumber = "密码不能为空";
          } else if (this.PasswordSet != this.AffirmPassword) {
            this.PasswordTrue = true;
            this.passwordNumber = "密码不一样";
          } else {
            this.PasswordTrue = false
          }
        }
      },
      //密码眼睛
      PasswordShow(i) {
        // i=="1"?():(this.PasswordAffirm==true?this.PasswordAffirm==false:this.PasswordAffirm==true)
        switch (i) {
          case 1:
            this.SetPassword == true ? this.SetPassword = false : this.SetPassword = true
            break;
          case 2:
            this.PasswordAffirm == true ? this.PasswordAffirm = false : this.PasswordAffirm = true
            break;
          default:
            break;
        }
      },
    },
    created() {

    }
  }
</script>

<style>
  .Forget .AccessEvents {
    color: #f90
  }

  .Forget .reminder {
    position: absolute;
    left: 0;
    bottom: -0.4rem;
    color: red;
    font-size: 0.24rem;
  }

  /*.filling-in{border-bottom: 1px solid #f90;height: 1rem;margin-bottom: 0.2rem;position: relative}*/
  .Forget .filling-in .fillingLeft {
    color: #666666;
    font-size: 0.34rem;
    height: 100%;
    display: inline-block;
    line-height: 1rem;
    float: left;
    width: 1.40rem;
  }

  .Forget .filling-in .fl input {
    border: none;
    color: #333333;
    font-size: 0.32rem;
    outline: none;
    vertical-align: text-bottom;
    padding-left: 0.3rem;
  }

  .Forget .filling-in .icon_paaword {
    float: right;
    width: 0.44rem;
    height: auto;
    line-height: 1rem;
    margin-right: 0.2rem;
  }

  .Forget .filling-in .icon_paaword img {
    width: 100%;
    vertical-align: middle;
  }

  .Forget .filling-in .icon-verification {
    float: right;
    height: auto;
    line-height: 1rem;
    width: auto
  }

  .Forget .filling-in .icon-verification img {
    width: 100%;
    vertical-align: middle;
    width: 1.4rem
  }

  /*.Forget .border-bD8D8D8 {*/
  /*  border-bottom: 1px solid #D8D8D8;*/
  /*}*/
  .Forget .filling-in .icon-verification span {
    line-height: 0.7rem;
    text-align: center;
    font-size: 0.34rem;
    display: inline-block;

  }
</style>



